<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="applycontent">
          <p class="apl_p">{{$t('data.info')}}</p>
          <div class="app_list">
            <span class="app_label">{{$t('data.compayname')}}</span>
            <input type="text" v-model="applydata.company" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.contactaddress')}}</span>
            <input type="text" v-model="applydata.address" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.Businessnumber')}}</span>
            <input type="text" v-model="applydata.companycard" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.Uploadbusiness')}}</span>
            <div class="uploadimg" :class="{'uploadimg_bj':!img1}">
              <input type="file" name="file2" accept="image/*" @change="fileImage1" /> 
              <img :src="img1" alt="">
            </div>
          </div>

          <p class="apl_p mt20">{{$t('data.Corporaterepresentative')}}</p>
          <div class="app_list">
            <span class="app_label">{{$t('data.representativeName')}}</span>
            <input type="text" v-model="applydata.name" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.contact')}}</span>
            <input type="text" v-model="applydata.phone" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.email')}}</span>
            <input type="text" v-model="applydata.email" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.Idnumber')}}</span>
            <input type="text" v-model="applydata.cardid" class="app_inp"/>
          </div>
          <div class="app_list">
            <span class="app_label">{{$t('data.Idphoto')}} ( {{$t('data.cardbackorfont')}} )</span>
            <div class="uploadimg" :class="{'uploadimg_bj':!img2}">
              <input type="file" name="file2" accept="image/*" @change="fileImage2" /> 
              <img :src="img2" alt="">
            </div>
            <div class="uploadimg" :class="{'uploadimg_bj':!img3}">
              <input type="file" name="file2" accept="image/*" @change="fileImage3" /> 
              <img :src="img3" alt="">
            </div>
          </div>

          <p class="apl_p mt20">{{$t('data.Application')}}</p>
          <div class="app_list">
            <span class="app_label">{{$t('data.download')}}</span>
            <input type="text" v-model="applydata.durl" class="app_inp"/>
          </div>

          <button class="app_btn" 
            :class="{'app_btn_bj' : applydata.company && applydata.address && applydata.companycard && applydata.name && applydata.phone && applydata.email && applydata.cardid && applydata.durl}" 
            @click="applydata.company && applydata.address && applydata.companycard && applydata.name && applydata.phone && applydata.email && applydata.cardid && applydata.durl && ApplyUpdate()">{{$t('code.ok')}}</button>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
export default {
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('data.Apply'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      img1:'',
      img2:'',
      img3:'',
      applydata:{
        company:'',
        address:'',
        companycard:'', // 营业执照号
        companycardpic:'', // 营业执照图片
        name:'',
        phone:'',
        email:'',
        cardid:'', // 身份证号
        cardf:'', // 身份证正面
        cardb:'', // 身份证反面
        durl:'',
        req:1
      }
    }
  },
  methods:{
    ApplyUpdate(){
      var that = this;
      var reg = /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/
      var phone = that.applydata.phone
      if(!reg.test(phone)){
        that.Toast(that.$t('data.correctphonenumber'))
        return;
      }
      var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
      var email = that.applydata.email
       if(!reg.test(email)){ 
    　　　　that.Toast(that.$t('data.correctemail'))
    　　　　return;
    　　}
      this.openloading(that.$t('data.submitting')) 
      this.axios({
        url:this.Url + 'AppApply.ashx',
        method:'post',
        params: this.applydata
      }).then(function (res) {
        that.closeloading()
        if(res.data.success){
          that.Toast(res.data.msg)
          that.applydata = {
            company:'',
            address:'',
            companycard:'', // 营业执照号
            companycardpic:'', // 营业执照图片
            name:'',
            phone:'',
            email:'',
            cardid:'', // 身份证号
            cardf:'', // 身份证正面
            cardb:'', // 身份证反面
            durl:'',
            req:1
          }
          that.img1=''
          that.img2=''
          that.img3=''
        }else{
          that.Toast(res.data.msg)
        }
      })
    },
    fileImage1(e){
      var that=this;
      var file = e.target.files[0];
      var imgSize=file.size/1024;
      var reader = new FileReader();
          reader.readAsDataURL(file); // 读出 base64
          reader.onloadend = function () {
            var dataURL = reader.result;
            that.img1 = dataURL
            that.uoloadfile(dataURL,1)
          }
    },
    fileImage2(e){
      var that=this;
      var file = e.target.files[0];
      var imgSize=file.size/1024;
      var reader = new FileReader();
          reader.readAsDataURL(file); // 读出 base64
          reader.onloadend = function () {
            var dataURL = reader.result;
            that.img2 = dataURL
            that.uoloadfile(dataURL,2)
          }
    },
    fileImage3(e){
      var that=this;
      var file = e.target.files[0];
      var imgSize=file.size/1024;
      var reader = new FileReader();
          reader.readAsDataURL(file); // 读出 base64
          reader.onloadend = function () {
            var dataURL = reader.result;
            that.img3 = dataURL
            that.uoloadfile(dataURL,3)
          }
    },
    uoloadfile(dataURL,num){
      var that = this
      var formData = new FormData()
      formData.append('file', dataURL)
      var url = that.Url + 'UploadFile.ashx'
      that.axios.post(url,formData,{ header: {"Content-Type": "multipart/form-data"}}).then(res => {
        if(res.data.success){
          if(num == 1){
            that.applydata.companycardpic = res.data.data
          }else if(num == 2){
            that.applydata.cardf = res.data.data
          }else if(num == 3){
            that.applydata.cardb = res.data.data
          }
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  padding-bottom: .3rem;
}
.mt20{
  margin-top: .3rem;
}
.apl_p{
  background: #dfeffd;
  font-size: .3rem;
  color: #278eed;
  text-align: center;
  padding: .2rem 0
}
.app_list{
  padding: 0 .3rem;
  margin-top: .3rem;
}
.app_list .app_label{
  display: block;
  color: #151515;
  font-size: .26rem;
}
.app_list .app_inp{
  display: block;
  color: #666;
  font-size: .26rem;
  margin-top: .1rem;
  border-radius: 4px;
  border: 1px solid #e4e4e4;
  height: .6rem;
  padding: 0 .3rem;
  text-align: left;
  line-height: .6rem;
  width: 100%
}
.uploadimg{
  width: 1.48rem;
  height: 1.48rem;
  border: 1px solid #e4e4e4;
  text-align: center;
  line-height: 1.5rem;
  color: #999;
  font-size: .3rem;
  margin-top: .1rem;
  cursor: pointer;
  display: inline-block;
  margin-right: .2rem;
}
.app_btn{
  font-size: .3rem;
  width: 4.58rem;
  height: .8rem;
  line-height: .8rem;
  text-align: center;
  border: none;
  outline: none;
  margin: .5rem auto;
  display: block;
  background: #f1f1f1;
  border-radius: 4px;
  color: #888;
}
.app_btn_bj{
  background: #278eed!important;
  color: #fff;
}
.uploadimg{
  position: relative;
  overflow: hidden;
}
.uploadimg_bj{
  background: url('../assets/images/addimg.png') no-repeat center center;
  background-size: .8rem .8rem;
}
.uploadimg input{
  position: absolute;
  top: 0;
  left: 0;
  width: 1.48rem;
  height: 1.48rem;
  opacity: 0;
}
.uploadimg img{
  width: 100%;
}
</style>



